import React, { useEffect, useRef, useState } from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { basicSetup } from '@codemirror/basic-setup';
import { EditorState } from '@codemirror/state';
import { EditorView, keymap } from '@codemirror/view';
import { defaultTabBinding } from '@codemirror/commands';
import { html } from '@codemirror/lang-html';
import { oneDark } from '@codemirror/theme-one-dark';
import parserHtml from 'prettier/parser-html';
import parserBabel from 'prettier/parser-babel';
import prettier from 'prettier/standalone';
import { useHotkeys } from 'react-hotkeys-hook';

import './styles.scss';
import { selectContent, selectUpdateLocalContent } from '../../store/CodePanel/selectors';
import { setPopUp } from '../../store/DossierSelectionPopUp/actions';
import {
  updateContent,
  saveContentToSession,
  switchEventAndCodeDisplay,
} from '../../store/CodePanel/actions';
import { operationItemDataType } from '../../constants';
import { selectDarkToggle } from '../../store/NavigationBar/selectors';
import { setReload } from '../../store/PreviewPanel/actions';
import ButtonTooltip from '../Tooltip/buttonTooltip';
import { isMacOs } from '../../utils/getOperatingSystem';
import {
  RELOAD_HOT_KEY_MACOS,
  RELOAD_HOT_KEY_WINDOWS_OR_LINUX,
  RUN_CODE_HOT_KEY_CODEMIRROR_MAC,
  RUN_CODE_HOT_KEY_CODEMIRROR_WINDOWS_OR_LINUX,
} from '../../constants/hotkeys';

// Component containing the code editor
const CodePanel = function ({
  propsContent,
  propsTheme,
  onUpdateContent,
  onSaveContentToSession,
  setIsOpenPopUp,
  onSetReload,
  updateLocalContent,
}) {
  // Reference to the editor dom element
  const editor = useRef();
  // Reference to the EditorView of editor dom element
  const view = useRef();

  const [isDraggingOver, setIsDraggingOver] = useState(false);

  // onReload event handler for 'ctrl+s/cmd+s'
  const onReload = (event) => {
    // prevent the default browser's save behavior
    event.preventDefault();
    onSetReload(true);
  };
  const reloadHotKey = isMacOs() ? RELOAD_HOT_KEY_MACOS : RELOAD_HOT_KEY_WINDOWS_OR_LINUX;
  // added reload hotkey
  // `onRunCode` won't work here because we need to call `event.preventDefault()`.
  useHotkeys(reloadHotKey, onReload);

  // The onclick handler of "Run code" button.
  const onRunCode = () => {
    if (propsContent === view.current?.state.doc.toString()) {
      onSetReload(true);
    } else {
      onUpdateContent(view.current?.state.doc.toString(), true);
    }
  };

  // The onclick handler of "Copy all" button
  const onCopyAll = () => {
    navigator.clipboard.writeText(view.current?.state.doc.toString());
  };

  // The onclick handler of "Clear code" button
  const onClearCode = () => {
    view.current?.dispatch({
      changes: { from: 0, to: view.current.state.doc.length, insert: '' },
    });
  };

  // Scroll to the selection in codemirror 6 with selection at top.
  const scrollToSelection = () => {
    if (view.current) {
      let topPos = 0;
      let heightOfLine = 0;

      const currentSelectionAnchor = view.current.state.selection?.ranges[0]?.anchor;
      // Scroll to the selected range
      if (currentSelectionAnchor) {
        const selectedLine = view.current.visualLineAt(currentSelectionAnchor);
        heightOfLine = selectedLine?.height || 0;
        topPos = selectedLine?.top || 0;
      }

      const { scrollDOM } = view.current;
      const topDiff = topPos - scrollDOM.scrollTop;
      // If the topDiff is greater than the clientHeight - heightOfLine or negative it means the
      // selection isn't in view. We only scroll if the line isn't in view. We also override this check if
      // given a customPos.
      if (scrollDOM && (topDiff > scrollDOM.clientHeight - heightOfLine || topDiff < 0)) {
        scrollDOM.scrollTop = topPos;
      }
    }
  };

  // Formats the code in the editor using prettier
  const formatDocument = () => {
    if (view.current) {
      try {
        const formattedContent = prettier.format(view.current.state.doc.toString(), {
          parser: 'html',
          plugins: [parserHtml, parserBabel],
        });
        // The current selection position
        const currentSelectionAnchor = view.current.state.selection?.ranges[0]?.anchor;
        // Change the editor with new formattedContent and keep the current selected range
        view.current.dispatch({
          changes: { from: 0, to: view.current.state.doc.length, insert: formattedContent },
          selection: { anchor: currentSelectionAnchor },
        });
        scrollToSelection();
      } catch (e) {
        // Do nothing
        console.log('Document could not be formatted with error:', e);
      }
    }
  };
  // Generate keymap with the given input for windows, mac, and action
  const generateKeyMap = (windowsInput, macInput, action) => {
    return keymap.of([
      {
        key: windowsInput,
        mac: macInput,
        preventDefault: true,
        run() {
          action();
        },
      },
    ]);
  };

  // Extensions used in code mirror
  const lightModeExtensions = [
    basicSetup,
    html(),
    // Tab size of 2 to match with prettier tab sizes
    EditorState.tabSize.of(2),
    // Enable tabbing
    keymap.of([defaultTabBinding]),
    // custom keymaps
    // We still need to keep this because code mirror blocks our global keyevent listener.
    // So is iframe. That's why the hotkey won't work in iframe.
    generateKeyMap(
      RUN_CODE_HOT_KEY_CODEMIRROR_WINDOWS_OR_LINUX,
      RUN_CODE_HOT_KEY_CODEMIRROR_MAC,
      onRunCode,
    ),
  ];
  const darkModeExtensions = [
    ...lightModeExtensions,
    // Dark theme
    oneDark,
  ];
  // The current applied extensions in code mirror
  const [currentExtensions, setCurrentExtensions] = useState(lightModeExtensions);

  // This effect updates the code editor doc based on the redux store
  // we only change the store when:
  // 1) loading in the localStorage code,
  // 2) saving the current code editor doc(which is same as propsContent in this case)
  useEffect(() => {
    if (updateLocalContent) {
      // If the local content state, view.current.state.doc.toString, equals the props we are passing in we return without doing anything
      if (view.current && view.current?.state.doc.toString() === propsContent) return;
      let formattedPropsContent;
      try {
        formattedPropsContent = prettier.format(propsContent, {
          parser: 'html',
          plugins: [parserHtml, parserBabel],
        });
      } catch (e) {
        formattedPropsContent = propsContent;
        console.log('Document could not be formatted with error:', e);
      }
      if (view.current) {
        view.current.dispatch({
          changes: { from: 0, to: view.current.state.doc.length, insert: formattedPropsContent },
        });
      } else {
        const state = EditorState.create({
          doc: formattedPropsContent,
          extensions: currentExtensions,
        });
        view.current = new EditorView({ state, parent: editor.current });
      }
      // Keep content the same in redux just set updateLocalContent to false
      // This helps start over popup change the local changes you made without saving to redux store if propsContent is same
      onUpdateContent(propsContent, false);
      // In this instance it should scroll back to first line since selection is reset to first line
      scrollToSelection();
    }
  }, [propsContent, updateLocalContent]);

  // This effect handles theme changes in code mirror 6 related to dark mode toggle
  useEffect(() => {
    setCurrentExtensions(propsTheme ? darkModeExtensions : lightModeExtensions);
    if (view.current) {
      view.current.setState(
        EditorState.create({
          doc: view.current.state.doc || '',
          selection: view.current.state.selection,
          extensions: propsTheme ? darkModeExtensions : lightModeExtensions,
        }),
      );
    }
  }, [propsTheme]);

  // This effect handles saving the code to local storage
  useEffect(() => {
    const state = EditorState.create({
      doc: '',
      extensions: currentExtensions,
    });
    view.current = new EditorView({ state, parent: editor.current });
    const saveCode = () => {
      if (document.visibilityState === 'hidden') {
        onSaveContentToSession(view.current?.state.doc.toString());
      }
    };
    // Save code when switching tabs, or closing tab. Visibilitychange works on mobile too unlike pagehide/onunload.
    document.addEventListener('visibilitychange', saveCode);
    return () => {
      document.removeEventListener('visibilitychange', saveCode);
    };
  }, []);

  const onDragOver = (e) => {
    if (view.current) {
      const curPos = view.current.posAtCoords({ x: e.pageX, y: e.pageY });
      view.current.dispatch({ selection: { anchor: curPos } });
    }
    setIsDraggingOver(true);
    e.preventDefault();
    e.stopPropagation();
  };
  const onDragLeave = () => {
    setIsDraggingOver(false);
  };
  const onDrop = (e) => {
    setIsDraggingOver(false);
    const code = e.dataTransfer.getData(operationItemDataType);
    if (code && view.current) {
      const curPos = view.current.posAtCoords({ x: e.pageX, y: e.pageY });
      const curLine = view.current.state.doc.lineAt(curPos);
      // Insert code at start of current line and select line
      view.current.dispatch({
        changes: { from: curLine.from, insert: code },
        selection: { anchor: curPos },
      });

      // Can improve this to so it doesnt change whole doc
      formatDocument();
      // Focus on the editor when dropping the code
      view.current.focus();
    }
    e.preventDefault();
    e.stopPropagation();
  };

  return (
    <div className={propsTheme ? 'code-mirror card-shadow dark' : 'code-mirror card-shadow'}>
      <div className="panel-header card-header">
        <span className="title">Editor</span>
        <div className="buttons">
          <button
            data-tip
            data-for="button__run__tooltip"
            className="button__run"
            onClick={onRunCode}
          >
            <span className="material-icons">play_arrow</span>
            <p>Run code</p>
          </button>
          <ButtonTooltip
            id="button__run__tooltip"
            getContent={() => (
              <div className="tooltip-text">{isMacOs() ? 'Command()+s' : 'Ctrl+s'}</div>
            )}
          />
          <button data-tip data-for="button__copy__tooltip" onClick={onCopyAll}>
            <span className="material-icons">copy_all</span>
          </button>
          <ButtonTooltip
            id="button__copy__tooltip"
            getContent={() => <div className="tooltip-text">Copy code</div>}
          />
          <button data-tip data-for="button__clear__tooltip" onClick={onClearCode}>
            <span className="material-icons">delete</span>
          </button>
          <ButtonTooltip
            id="button__clear__tooltip"
            getContent={() => <div className="tooltip-text">Clear code</div>}
          />
          <button data-tip data-for="button__format__tooltip" onClick={formatDocument}>
            <span className="material-icons">format_align_left</span>
          </button>
          <ButtonTooltip
            id="button__format__tooltip"
            getContent={() => <div className="tooltip-text">Format</div>}
          />
        </div>
      </div>
      <div
        className={isDraggingOver ? 'code-mirror-container dragging' : 'code-mirror-container'}
        ref={editor}
        onDragOver={onDragOver}
        onDragLeave={onDragLeave}
        onDrop={onDrop}
      />
    </div>
  );
};

function mapStateToProps(state) {
  return {
    propsContent: selectContent(state),
    propsTheme: selectDarkToggle(state),
    updateLocalContent: selectUpdateLocalContent(state),
  };
}

function mapDispatchToProps(dispatch) {
  return bindActionCreators(
    {
      onUpdateContent: updateContent,
      onSaveContentToSession: saveContentToSession,
      setIsOpenPopUp: setPopUp,
      onSetReload: setReload,
      onSwitchCodeAndEventDisplay: switchEventAndCodeDisplay,
    },
    dispatch,
  );
}

export default connect(mapStateToProps, mapDispatchToProps)(CodePanel);
